.App {
  text-align: center;
}
ul {
  width: 100%;
  list-style: none;
  padding: 0 0.25rem 0.25rem;
}
ul li::before {
  content: "\200B";
}
.item {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  text-align:left;
  padding: 0.5rem 0 0.5rem 0.5rem;
  margin: 0.25rem 0;
  background-color: #ffffff29;
  border-radius: 10px;
  box-shadow: 0px 0px 20px -5px rgba(220, 53, 70, 0.433);
}
.item:first-child {
  margin: 0;
}

input[type="text"] {
  padding: 0.5rem 0 0.5rem 0.5rem;
  margin: 0.25rem 0;
  height: 50px;
  background-color: #ffffff29;
  box-shadow: 0px 0px 20px -5px rgba(220, 53, 70, 0.433);
  border-radius: 10px;
  border: 0px;
  width: 350px;
}

.item input[type="checkbox"] {
  text-align: center;
  width: 2.5rem;
  width: 18px;
  min-width: 18px;
  height: 18px;
  height: 18px;
  min-height: 48px;
  cursor: pointer;
  margin-right: 0.5rem;
}


.item > label {
  font-size: 20px;
  flex-grow: 1;
  width: 80%;
}

.item svg {
  width: 48px;
  min-width: 48px;
  height: 36px;
  font-size: 1rem;
  color: rgb(255, 255, 255);
  cursor: pointer;
}

.item svg:focus, 
.item svg:hover {
  color: rgb(207, 207, 207);
  outline: none;
}

button {
  height: 48px;
  min-width: 48px;
  border-radius: 0.25rem;
  padding: 0.5rem;
  font-size: 1rem;
  background-color: aliceblue;
  color: mediumblue;
  cursor: pointer;
}

button:focus, 
button:hover {
  color: white;
  background-color: limegreen;
  outline: none;
}
.inp-btn{
  display: flex;
  flex-wrap: nowrap;
  justify-content: center!important;
  align-items: center!important;
}

.footer{
  width: 100%;
  height: 50px;
 position: fixed;
  bottom: 0px;
}
p{
  font-size: 16px;
  font-weight: 600;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  color: aliceblue;
}